.videoCard {
    margin-bottom:40px; 
    width: 270px;
}

.videoCard_thumbnail {
    height: 140px;
    width: 250px;
}

.videoCard_info {
    display: flex;
    margin-top: 3px;
    padding-right: 30px;
}

.videoCard_text {
    margin-left: 15px;
}

.videoCard_text > h4 {
    font-size:14px;
    margin-bottom: 0px;
    margin-top:0px;
}

.videoCard_text > p {
    margin-top:0px;
    margin-bottom:0px;
    font-size: 14px;
    color:gray;
}

.videoCard_avatar {
    height: 30px !important;
    width: 30px !important;
}

